@font-face {
  font-family: 'Manrope';
  src: url(../public/fonts/Manrope.ttf);
}

@font-face {
  font-family: 'Inter';
  src: url(../public/fonts/InterVariable.ttf);
}

body {
  background-color: rgb(0, 0, 0);
  margin: 0;
  overflow-x: hidden;
}

h1,
h2,
h3,
p,
a,
label {
  font-family: 'Manrope', sans-serif;
}

h1 {
  margin: -20px 0px;
  font-size: 500%;
}

h2 {
  font-size: 200%;
  margin: 0px;
  font-weight: 500;
}

.app-container {
  width: 100vw;
  min-height: 100vh;
}

.hoverable {
  transition: .2;
}

.header {
  width: 100vw;
  height: 42px;
  padding: 20px;
  position: fixed;
  top: -83px;
  left: 0;
  background-color: rgba(255, 255, 255, 0.5);
  backdrop-filter: 20px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.2);
  display: flex;
  flex-direction: row;
}

.min-logo {
  height: 42px;
  width: 42px;
  background-image: url(../public/images/min-logo.png);
  background-position: center;
  background-size: contain;
  background-repeat: no-repeat;
}

.main-promo-block {
  height: 100svh;
  width: calc(100vw - 80px);
  background-image: url(../public/images/grid.webp), linear-gradient(33deg, rgba(89, 0, 255, 1) 0%, rgb(10 11 12) 64%);
  background-blend-mode: overlay;
  background-size: cover;
  padding: 0px 40px;
  overflow: hidden;
}

.content-block {
  height: 100svh;
  width: calc(100vw - 80px);
  padding: 0px 40px;
  background-color: black;
  overflow: hidden;
}

#synaptiq-logo {
  background-image: url(../public/images/Synaptiq.png);
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  height: 50px;
  width: 280px;
  position: absolute;
  left: 50%;
  top: 20px;
  transform: translate(-50%, 0px);
  filter: invert();
}

.row {
  display: flex;
  flex-direction: row;
  width: 100%;
}

.column {
  display: flex;
  flex-direction: column;
  width: fit-content;
  height: max-content;
}

.big-avatar {
  margin-right: 12px;
  height: 160px;
  width: 160px;
  min-width: 160px;
  border-radius: 50%;
  background-size: cover;
  background-position: center;
  background-image: url(../public/images/avatar.jpg);
  filter: drop-shadow(0px 10px 20px rgba(0, 0, 0, 0.1));
}

#main-card {
  position: absolute;
  height: -moz-fit-content;
  height: fit-content;
  bottom: -100px;
  left: 50%;
  width: max-content;
  z-index: 10;
  /* margin: 0 auto; */
  transform: translate(-50%, 0px);
}

.inner-container {
  width: 100%;
  height: 100%;
  max-width: 1500px;
  margin: 0 auto;
  position: relative;
}

.info-card {
  height: 160px;
  width: -moz-fit-content;
  width: fit-content;
  border-radius: 10px;
  background-color: rgb(0 0 0 / 43%);
  color: rgb(255, 255, 255);
  display: flex;
  justify-content: center;
  flex-direction: column;
  padding: 0px 40px;
  border: 1px solid rgb(255 255 255 / 10%);
  backdrop-filter: blur(6px);
  -webkit-backdrop-filter: blur(6px);
  -moz-backdrop-filter: blur(6px);
}

.info-card {
  transition: transform 0.3s ease;
}

.info-card.hovered {
  transform-style: preserve-3d;
}

.info-card h1 {
  font-size: 320%;
  opacity: .9;
  font-weight: 500;
}

.info-card h1 {
  margin-bottom: -10px;
}

.centered {
  align-items: center;
}

#words-slider {
  height: 500px;
  width: 570px;
  position: relative;
}

#words-slider .word {
  width: max-content;
  margin: -4px 0px;
  transition: .5s;
  color: white;
  position: absolute;
  top: 50%;
  transform: translate(0px, -50%);
  font-weight: 500;
  font-size: 600%;
}

.word.w0,
.word.w4 {
  opacity: .0;
}

.word.w1,
.word.w3 {
  opacity: .2;
}

.word.w2 {
  opacity: 1;
}

.word.w4 {
  transform: translate(0px, -50%) translate(0px, -200px) !important;
  filter: blur(20px);
}

.word.w3 {
  transform: translate(0px, -50%) translate(0px, -100px) !important;
  filter: blur(4px);
}

.word.w2 {
  transform: translate(0px, -50%) translate(0px, 0px) !important;
}

.word.w1 {
  transform: translate(0px, -50%) translate(0px, 100px) !important;
  filter: blur(4px);
}

.word.w0 {
  transform: translate(0px, -50%) translate(0px, 200px) !important;
  filter: blur(20px);
}

#main-slider {
  align-items: center;
  width: fit-content;
  position: absolute;
  top: calc(50% - 50px);
  left: 50%;
  transform: translate(-50%, calc(-50% - 60px));
}

#main-slider>h1 {
  margin: 0px;
  margin-bottom: 8px;
  margin-right: 26px;
  font-weight: 500;
  font-size: 600%;
  color: white;
}

.icon {
  height: 26px;
  width: 26px;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}

#slide-up-line {
  position: absolute;
  top: -66px;
  left: 50%;
  transform: translate(-50%, 0px);
  color: white;
  width: fit-content;
  opacity: 1 !important;
  transition: .3s ease;
}

.slided #slide-up-line {
  top: 0px;
}

#slide-up {
  width: calc(100vw - 80px);
  height: 30px;
  left: 0;
  background-color: rgba(0, 0, 0, 0);
  color: rgb(255, 255, 255);
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 50px 40px;
  /* border: 1px solid rgba(255, 255, 255, 0); */
  backdrop-filter: blur(0px);
  -webkit-backdrop-filter: blur(0px);
  -moz-backdrop-filter: blur(0px);
  position: absolute;
  bottom: -110px;
  transition: .3s;
}

body:has(.main-menu.opened) #slide-up {
  transition: .5;
  opacity: 0;
}

#slide-up>*:not(#slide-up-line) {
  opacity: 0;
  transition: opacity 0.3s ease;
}

#slide-up.slided>* {
  opacity: 1 !important;
}


#slide-up.slided {
  background-color: rgb(0, 0, 0);
  color: rgb(255, 255, 255);
  /* border: 1px solid rgb(255 255 255 / 18%); */
  backdrop-filter: blur(6px);
  -webkit-backdrop-filter: blur(6px);
  -moz-backdrop-filter: blur(6px);
  position: absolute;
  bottom: 0px;
}

#menu-button {
  position: fixed;
  top: 30px;
  right: 40px;
  width: 40px;
  height: 40px;
  background-size: contain;
  z-index: 950;
}

.label-with-icon .icon {
  margin-right: 4px;
}

.link {
  color: white;
  text-decoration: none;
  margin: -10px;
  padding: 10px;
  background-color: transparent;
  transition: .2s;
  border-radius: 8px;
}

.link:hover {
  background-color: rgba(255, 255, 255, 0.1);
}

#social-panel p {
  margin: 0px;
}

#social-panel {
  display: flex;
  align-items: center;
  flex-direction: row;
  margin-top: 20px;
}

#social-panel .link {
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-right: 16px;
}

#social-panel .icon {
  margin-right: 8px;
}

.skill-card {
  background-color: rgba(255, 255, 255, 0.1);
  border-radius: 10px;
  height: calc(100% - 40px);
  min-width: calc(50% - 46px);
  margin-right: 12px;
  color: white;
  padding: 20px;
  position: relative;
  cursor: pointer;
  border: 1px solid rgb(255 255 255 / 10%);
}

.skill-card:hover {
  background-color: rgba(255, 255, 255, 0.15);
}

.skill-image {
  position: absolute;
  top: calc(50% - 40px);
  right: 0px;
  transform: translateY(-50%);
  opacity: .3;
  width: 300px;
  height: calc(100% - 60px);
  background-size: contain;
  background-position: right center;
  background-repeat: no-repeat;
}

.skills-block {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 70%;
}

.skills-block .row {
  height: calc(50% - 12px);
  margin-bottom: 12px;
}

.skill-card:last-child {
  margin-right: 0px;
}

.skill-card h1,
.wide-card h1 {
  font-size: 36px;
  margin: 0px;
}

.skill-card h2,
.wide-card h2 {
  font-size: 20px;
}

.plus-button {
  position: absolute;
  bottom: 10px;
  right: 10px;
  height: 50px;
  width: 50px;
  border-radius: 50%;
  background-image: url(../public/icons/plus.png);
  background-size: 60%;
  background-color: black;
  background-position: center;
  background-repeat: no-repeat;
  transition: .2s;
}

.skill-card:hover .plus-button {
  filter: invert();
}

.myBubbleUI {
  width: 750px;
  height: 540px;
  position: absolute;
  top: 0px;
  right: 20px;
}

.bubble {
  width: 100%;
  height: 100%;
  border-radius: 50%;
  background-color: rgba(255, 255, 255, 0.1);
  color: rgb(255, 255, 255);
  display: flex;
  align-items: center;
  justify-content: center;
  transition: .2s !important;
  border: 1px solid rgb(255 255 255 / 10%);
}

.bubble:hover {
  background-color: rgba(255, 255, 255, 0.15);
}

.bubble .icon {
  height: 50px;
  width: 50px;
  border-radius: 12px;
  margin-bottom: 10px;
  background-color: black;
  filter: invert();
  padding: 16px;
  background-size: 80%;
}

.bubble .column {
  align-items: center;
}

.bubble p {
  margin: 0px 0px -10px 0px;
}

.wide-card {
  width: calc(100% - 40px);
  height: 500px;
  background-color: rgba(255, 255, 255, 0.1);
  border-radius: 10px;
  padding: 20px;
  color: white;
  position: relative;
  border: 1px solid rgb(255 255 255 / 10%);
}

.tags-cloud {
  width: 80%;
  display: flex;
  flex-direction: row;
  height: max-content;
  margin: 14px 0px;
  flex-wrap: wrap;
}

.tag {
  min-width: max-content;
  height: fit-content;
  padding: 10px 14px;
  border-radius: 10px;
  background-color: rgba(255, 255, 255, 0.1);
  margin: 0px 10px 10px 0px;
}

.tag p {
  margin: 0px 10px 0px 0px;
}

.tag .icon {
  padding: 6px;
  height: 12px;
  width: 12px;
  background-color: black;
  border-radius: 6px;
  filter: invert();
  background-size: 80%;
}

.middle-width {
  width: 50%;
}

.centered-container {
  height: 100%;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.main-menu {
  height: calc(100svh - 60px);
  width: calc(50% - 20px);
  position: fixed;
  padding: 20px;
  top: 10px;
  right: 10px;
  background-color: rgba(0, 0, 0, 0.5);
  -webkit-backdrop-filter: blur(54px);
  backdrop-filter: blur(54px);
  transition: transform cubic-bezier(0.73, 0.02, 0.37, 1.03) 500ms 0ms, filter 300ms 200ms, opacity 500ms 0ms;
  z-index: 900;
  transform: translate(calc(100% + 10px), 0%);
  display: flex;
  flex-direction: row;
  justify-content: center;
  border-radius: 10px;
  border: 1px solid rgba(255, 255, 255, 0.1);
  flex-wrap: wrap;
  align-content: center;
}

body:has(.main-menu.closed) .over-layer {
  transition: opacity .3s .2s, height 0s .5s;
  height: 0svh;
  opacity: 0;
}

.inner-container {
  transition: .4s .1s;
}

body:has(.main-menu.opened) .inner-container,
body:has(.article-window.visible) .inner-container {
  transform: scale(0.92);
}

.main-menu.opened {
  transition: transform cubic-bezier(0.73, 0.02, 0.37, 1.03) 500ms 0ms, filter 300ms 0ms, opacity 500ms 0ms;
  filter: blur(0px);
  transform: translate(0, 0);
}

/* .main-menu.closed .menu-element{
  filter: blur(10px);
  transform: scale(0.9);
} */

.menu-element {
  padding: 20px;
  width: calc(50% - 40px - 14px);
  height: 15%;
  min-height: 100px;
  background-color: rgb(255 255 255 / 10%);
  border-radius: 10px;
  margin: 0px 12px 12px 0px;
  border: 1px solid rgb(255 255 255 / 10%);
  transition: .2s;
  cursor: pointer;
  position: relative;
}

.over-layer {
  position: fixed;
  bottom: -1px;
  left: 0px;
  height: calc(100svh + 1px);
  width: 100vw;
  backdrop-filter: blur(8px);
  z-index: 500;
  opacity: 1;
  overflow: hidden;
  transition: opacity .3s .2s, height 0s 0s;
}

.window-tint {
  position: absolute;
  top: 0px;
  left: 0px;
  height: 100%;
  width: 100%;
  backdrop-filter: blur(4px);
  z-index: 1;
  background-color: rgba(0, 0, 0, 0.6);
  background: linear-gradient(33deg, rgb(89 0 255 / 40%) 0%, rgb(10 11 12 / 50%) 64%);
}

.window-back {
  display: flex;
  position: fixed;
  top: 0px;
  left: 0px;
  height: 100svh;
  width: 100vw;
  z-index: 1000;
  opacity: 1;
  overflow: hidden;
  transition: opacity .3s .0s, height 0s 0s;
  justify-content: center;
  align-items: center;
}

.menu-element.hidden-element {
  filter: blur(10px);
  transform: translate(60px, 0px);
}

.menu-element:hover {
  background-color: rgb(255 255 255 / 15%);
}

.menu-element p {
  color: white;
  margin: 0px;
  font-size: 30px;
}

.menu-element .icon {
  position: absolute;
  bottom: 20px;
  left: 20px;
}

.hoverable:hover {
  transform: scale(1.01);
}

.clickable {
  cursor: pointer;
  transition: .2s;
}

.clickable:active {
  transform: scale(0.9);
}

.icon.rounded {
  border-radius: 50%;
}

.article-window {
  /* top: 50%; */
  /* left: 50%; */
  /* transform: translate(-50%, -50%); */
  position: relative;
  color: white;
  max-width: 1460px;
  width: calc(100% - 140px);
  height: 70%;
  padding: 20px;
  background-color: rgba(255, 255, 255, 0.1);
  -webkit-backdrop-filter: blur(34px);
  backdrop-filter: blur(34px)!important;
  transition: transform ease-out 400ms 0ms, opacity 400ms 0ms, filter 300ms 200ms;
  z-index: 2;
  border-radius: 10px;
  overflow: hidden;
  border: 1px solid rgba(255, 255, 255, 0.1);
}

.article-window.invisible {
  transition: transform ease-in 500ms 0ms, opacity 500ms 0ms, filter 300ms 100ms;
  opacity: 0;
  transform: scale(1.3);
}

.window-back:has(.article-window.invisible) {
  transition: height 0s .5s, opacity .5s .1s;
  opacity: 0;
  height: 0px;
}

.window-header {
  position: absolute;
  top: 0px;
  left: 0px;
  width: calc(100% - 40px);
  height: 44px;
  padding: 20px;
  display: flex;
  align-items: center;
}

.window-content {
  position: absolute;
  bottom: 0px;
  left: 0px;
  height: calc(100% - 104px);
  width: calc(100% - 40px);
  overflow-y: scroll;
  padding: 0px 20px 20px 20px;
}

.article-window:has(.semi-image) .window-content{
  width: calc(50% - 40px);
}

.close-button {
  background-color: black;
  background-image: url(../public/icons/close.png);
  background-size: 50%;
  background-position: center;
  background-repeat: no-repeat;
  height: 46px;
  width: 46px;
  border-radius: 50%;
  position: absolute;
  top: 20px;
  right: 20px;
  z-index: 10;
}

.click-area {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0px;
  left: 0px;
  touch-action: none;
  z-index: 10;
}

