.cards-slider{
  display: flex;
  width: calc(100% + 20px);
  margin-left: -20px;
  height: 70%;
  flex-direction: row;
  flex-wrap: wrap;
  position: relative;
  margin-bottom: -20px;
}


.perk-card-inner {
  position: relative;
  padding: 0px 20px 20px 20px;
  margin-bottom: 20px;
  width: calc(33.3% - 1px - 60px);
  height: calc(50% - 1px - 40px);
  border-right: 1px solid rgb(255 255 255 / 20%);
}

.perk-card-inner:nth-child(3n){
  border-right: 0px;
}

.perk-card-inner .icon{
  height: 100px;
  width: 100px;
  position: absolute;
  bottom: -16px;
  left: 20px;
  opacity: .8;
}

.perk-card-inner p{
  max-width: calc(100% - 0px);
  margin: 0px;
}

.main-modal-button{
  position: absolute;
  bottom: 20px;
  left: 20px;
  width: max-content;
  height: fit-content;
  padding: 14px 20px;
  border-radius: 8px;
  background-color: rgba(0, 0, 0, 0.8);
  border: 1px solid rgba(255, 255, 255, 0.15);
  cursor: pointer;
}

.main-modal-button p{
  margin: 0px;
}

.semi-image{
  position: absolute;
  top: 0px;
  right: 0px;
  width: 50%;
  height: 100%;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

.semi-image a{
  position: absolute;
  bottom: 20px;
  left: 20px;
  font-size: 12px;
  color: white;
  text-decoration: none;
  max-width: 200px;
  opacity: .9;
}

@keyframes back-gradient {
  0% {
    background: linear-gradient(33deg, rgba(89, 0, 255, 1) 0%, rgb(10 11 12) 64%);
  }

  50% {
    background: linear-gradient(66deg, rgba(89, 0, 255, 1) 0%, rgb(10 11 12) 64%);
  }

  100% {
    background: linear-gradient(33deg, rgba(89, 0, 255, 1) 0%, rgb(10 11 12) 64%);
  }
}